import React, { useContext } from 'react';
import { LanguageContext } from '../contexts/LanguageContext';

function Navigation({ activePage, setActivePage }) {
  const { language } = useContext(LanguageContext);

  const navItems = [
    { id: 'homepage', label: language === 'zh' ? '' : 'Homepage', icon: '' },
    { id: 'about', label: language === 'zh' ? '' : 'About', icon: '' },
    { id: 'version', label: language === 'zh' ? '' : 'Version', icon: '' },
    { id: 'strategy', label: language === 'zh' ? '' : 'Strategy', icon: '' },
    { id: 'driverroll', label: language === 'zh' ? '' : 'Driver Roll', icon: '' },
    { id: 'steward', label: language === 'zh' ? '' : 'Steward', icon: '' },
    { id: 'feedback', label: language === 'zh' ? '' : 'Feedback', icon: '' }
  ];

  return (
    <nav className="navigation">
      {navItems.map(item => (
        <button
          key={item.id}
          className={`nav-btn ${activePage === item.id ? 'active' : ''}`}
          onClick={() => setActivePage(item.id)}
        >
          <span className="icon">{item.icon}</span> {item.label}
        </button>
      ))}
    </nav>
  );
}

export default Navigation;